$columns: 12; // Number of columns in the grid system

.container {
	margin: 0 auto;
	max-width: 960px;
}

.container-fluid {
	max-width: 100%;
}

.row {
	display: grid;
	grid-template-columns: 290px 1fr 1fr;
	grid-template-rows: 1fr 1fr;
	height: 100%;
}

.case-2-cols {
	display: grid;
	grid-template-columns: 1fr 1fr;
}



[class^="col-"] {
	display: grid;
}


.col-left {
	grid-area: 1 / 1 / 2 / 2;
	grid-auto-rows: auto;
}

.col-center {
	grid-area: 1 / 2 / 2 / 3;
}

.col-right {
	grid-area: 1 / 3 / 2 / 4;
}

.col-bottom {
	position: relative;
	display: grid;
	min-height: 130px;
	grid-area: 2 / 1 / 3 / 4;
	grid-template-columns: 1fr 1fr 1fr 1fr;
}

.col_box {
	display: grid;
}


@media only screen and (max-width: 768px) {
	.row {
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr 1fr;
	}

	.col-left {
		grid-area: 1 / 1 / 2 / 2;
	}

	.col-center {
		grid-area: 2 / 1 / 3 / 2;
	}

	.col-right {
		grid-area: 3 / 1 / 4 / 2;
	}

	.col-bottom {
		grid-area: 4 / 1 / 5 / 2;
		grid-template-columns: 1fr 1fr 1fr 1fr;
	}

	.techne {
		grid-template-columns: 1fr !important;
		.dgrid.bl8 {
			border: none !important;
		}
	}



}