main {
  width: 100vw;
  height: 100vh;
  padding: $five;
  background: $white;
}

.frame {
  width: 100%;
  height: 100%;
  //display: flex;
  flex: 1;
  border: $line-height solid $black;
  overflow-y: auto;
  overflow-x: hidden;
}

.techne {
  grid-template-columns: 175px 1fr;
}

.middle-hack {
  margin: auto 0;
}

.social-network a {
  font-size: 21px;
  text-decoration: none;
}

.process {
  position: relative;
  counter-reset: section;
  margin-left: 13px;

  li:not(.line) {
    position: relative;
    display: flex;
    align-items: center;
    padding-bottom: $four;
    border-left: 1px solid $black;

    &:first-child {
      align-items: flex-start;
    }

    &:last-child {
      padding-bottom: 0;
    }

    &:before {
      counter-increment: section;
      content: counter(section);
      display: flex;
      flex-shrink: 0;
      align-items: center;
      justify-content: center;
      width: 26px;
      height: 26px;
      border-radius: 100%;
      margin-right: 15px;
      margin-left: -13px;
      line-height: 2;
      text-align: center;
      background-color: $black;
      color: $white;
    }
  }

}

.more-works {
  a {
    color: $black;
    text-decoration: none;
    background-size: cover;
    background-position: center top;

    &:hover {
      background: $theme;
      color: $black;
    }

  }

  ul {
    margin-top: 0;
  }

  &:before {

    content: 'SOME SHOTS';
    position: absolute;
    width: 100px;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    @extend .pt1;
    @extend .pb1;
    @extend .bl8;
    @extend .br8;
    @extend .bb8;
    background-color: $white;
    color: $black;
    font-family: $head-font;
    letter-spacing: 1px;
    font-size: 12px;
    text-align: center;
  }
}

.bg-image {
  background-size: cover;
  background-position: center;
}

.avatar {
  position: relative;
  min-height: 199px;
}

.avatar:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-image: url('../img/avatar.jpg');
  background-size: cover;
  background-position: center top;
  transition: $transition;
}

.case-study-label {
  display: block;
}

.case-study {
  display: none;
  position: absolute;
  width: 100vw;
  height: auto;
  top: 0;
  left: 0;
  padding: $five;
  background: $white;

  .close-case {
    @extend .flex-vhcenter;
    @extend .b8;
    width: $five;
    height: $five;
    margin-right: 5px;
    position: fixed;
    right: $five;
    top: $five;
    text-decoration: none;
    background-color: $white;
    z-index: 1000;
  }

  .frame {
    padding: $five;
    border: none;
  }

  .case_image {
    display: block;
    margin: 0 auto;
    max-width: 900px;
    width: 100%;
    height: auto;
  }

  &.opened {
    display: flex;
  }

}

.case-open {
  overflow-x: hidden;
  main {
    display: none;
  }
}

.article-text {
  max-width: 790px;
  margin: auto;
  font-size: 16px;

  h1 {
    margin-bottom: $six;
  }

  h2 {
    font-size: 28px;
    margin-bottom: $three;
  }

  h3 {
    font-size: 21px;
    margin: $five 0;
    @extend .pb2;
    border-bottom: 1px solid rgba(0, 0, 0, .4);
  }

  section {
    padding-bottom: $seven;
  }

  .meta {
    font-size: 12px;
    font-style: italic;
    margin-bottom: 6%;
  }
}

main.blog {
  height: auto;
}

.dgrid {
  display: grid;
  align-content: center;
}